<template>
  <div id="building">
    <div id="loginBox" style="height: 100vh; display: flex; align-items: center; justify-content: center;">
      <div style="display: flex; width: 35%; border-radius: 5px; overflow: hidden;height:60%">
        <div style="flex: 1; display: flex; align-items: center; justify-content: center;background-color:white;">
          <el-form :model="user" style="width: 70%" :rules="rules" ref="loginRef">
            <div style="font-size: 24px; font-weight: bold; text-align: center; margin-bottom: 20px;">欢迎登录后台管理系统</div>
            <el-form-item prop="account">
              <el-input prefix-icon="el-icon-user" size="medium" placeholder="请输入账号" v-model="user.account"></el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input prefix-icon="el-icon-lock" size="medium" show-password placeholder="请输入密码" v-model="user.password"></el-input>
            </el-form-item>
            <el-form-item prop="code">
              <div style="display: flex">
                <el-input placeholder="请输入验证码" prefix-icon="el-icon-circle-check" size="medium" style="flex: 1" v-model="user.code"></el-input>
                <div style="flex: 1; height: 36px">
                  <valid-code @update:value="getCode" />
                </div>
              </div>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" style="width: 100%" @click="login">登 录</el-button>
            </el-form-item>
            <div style="display: flex">
              <div style="flex: 1; text-align: right"><span style="color: #0f9876; cursor: pointer"  @click="handleForgetPass">忘记密码</span></div>
            </div>
          </el-form>
        </div>
      </div>

      <el-dialog title="忘记密码" :visible.sync="forgetPassDlalogVis" width="30%">
        <el-form :model="forgetUserForm" label-width="80px" style="padding-right: 20px">
          <el-form-item label="用户账号">
            <el-input v-model="forgetUserForm.account" autocomplete="off" placeholder="请输入用户账号"></el-input>
          </el-form-item>
          <el-form-item label="手机号">
            <el-input v-model="forgetUserForm.phone" autocomplete="off" placeholder="请输入手机号"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="forgetPassDlalogVis = false">取 消</el-button>
          <el-button type="primary" @click="resetPassword">确 定</el-button>
        </div>
      </el-dialog>

    </div>

  </div>
</template>

<script>
import ValidCode from "@/components/ValidCode";

export default {
  name: "Login",
  components: {
    ValidCode
  },
  data() {

    // 验证码校验
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'))
      // } else if (value.toLowerCase() !== this.code) {   //不区分大小写
      } else if (value!== this.code) {
        callback(new Error('验证码错误'))
      } else {
        callback()
      }
    }

    return {
      forgetUserForm:{},    //忘记密码的表单数据
      forgetPassDlalogVis:false,
      code: '',  // 验证码组件传递过来的code
      user: {
        code: '',   // 表单里用户输入的code 验证码
        account: '',
        password: ''
      },
      rules: {
        account: [
          { required: true, message: '请输入用户账号', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
        ],
        code: [
          { validator: validateCode, trigger: 'blur' }
        ],
      }
    }
  },
  created() {
    if(/Android/i.test(navigator.userAgent)) {
      const wPath = window.document.location.href;
      const pathName = this.$route.path;
      const pos = wPath.indexOf(pathName);
      const localhostPath = wPath.substring(0, pos);
      window.location.replace(localhostPath + '/apk/app.apk');
    }
  },
  methods: {
    handleForgetPass(){  //初始化表单的数据
      this.forgetUserForm={}
      this.forgetPassDlalogVis=true
    },
    resetPassword(){
      this.$request.put('/password',this.forgetUserForm).then(res => {
        if (res.code === '200'){
          this.$message.success('重置成功')
          this.forgetPassDlalogVis=false
        } else {
          this.$message.error(res.msg)
        }
      })
    },
    getCode(code) {
      this.code = code
    },
    login() {
      this.$refs['loginRef'].validate((valid) => {
        if (valid) {
          // 验证通过
          this.$request.post('/login', this.user).then(res => {
            if (res.code === '200') {
              this.$router.push('/')
              this.$message.success('登录成功')
              localStorage.setItem("honey-user", JSON.stringify(res.data))  // 存储用户数据
              //sessionStorage.setItem("honey-user", JSON.stringify(res.data))  // 存储用户数据,实现每次访问需要重新登录
            } else {
              this.$message.error(res.msg)
            }
          })
        }
      })
    }
  }
}
</script>

<style scoped>
#building{
  background:url("../assets/登录.png");
  width:100%;
  height:100%;
  position:fixed;
  background-size:100% 100%;
}

</style>